<template>
<div>
	<!--<header :class="{'no-border':data.noborder}">-->
	<header>
		<a class="ps-back" @click="back" href="javascript:;"></a>
		<h2>{{data.title}}</h2>
	</header>
	<div class="header_seat"></div>
</div>
</template>

<script>
	export default {
		name: '',
		props: {
			data: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			}
		},
		data: () => ({
			
		}),
		computed: {
			
		},
		methods: {
			back() {
				var _this = this;
				if(_this.data.isBack){
					history.go(-1);
				}else{
					_this.common.Compatible({
						paraIos: {
							'route': 'aabb',
							'url': 'back'
						},
						fnAndroid: function() {
							window.android.onBackPressed();
						},
						fn: function() {
							console.warn('error');
						}
					});
				}
			}
		}
	}
</script>

<style scoped>
	header {
		box-sizing: border-box;
	    position: fixed;
	    top: 0;
	    left: 0;
	    width: 100%;
	    height: .88rem;
	    background: #fff;
	    z-index: 100;
	    /*border-bottom: 1px solid #CCC;*/
	    box-shadow: 0 .02rem .04rem rgba(186,186,186,0.15);
	}
	header.no-border{
		border-bottom: none;
	}
	header .ps-back {
	    position: absolute;
	    top: 0;
	    bottom: 0;
	    left: 0;
	    display: block;
	    width: .88rem;
	    height: .88rem;
	    background: url(images/ic_back_black@2x.png) no-repeat .3rem center;
	    background-size: .36rem .28rem;
	}
	header>h2{
		font-size: .34rem;
	    line-height: .88rem;
	    overflow: hidden;
	    height: .88rem;
	    padding: 0 .88rem;
	    text-align: center;
	    white-space: nowrap;
	    text-overflow: ellipsis;
	    color: #333;
	}
	header+.header_seat {
	    height: .88rem;
	}
</style>


