<template>
	<li :class="{'is-spread': isOpen}" :data-id="data.id">
		<div @click="isOpen=!isOpen">{{data.title}}</div>
		<div v-show="isOpen">
			<div v-html="data.content">{{data.content}}</div>
		</div>
	</li>
</template>

<script>
	import '@/assets/js/layer041002.js';
	export default {
		name: '',
		props: {
			data: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			},
			dataindex: {
				type: [Number, String],
				default: 0
			}
		},
		data: () => ({
			'isOpen': false
		}),
		methods: {
			
		},
		computed: {

		}
	}
</script>

<style scoped>
	li{
		color: #666;
	}
	li>div:nth-of-type(1) {
		padding: .3rem .36rem 0;
		height: 1rem;
		background-color: white;
		box-sizing: border-box;
		position: relative;
	}
	
	li>div:nth-of-type(1)::after {
		content: '';
		position: absolute;
		top: 0;
		right: .36rem;
		width: .25rem;
		height: 1rem;
		background: url(../../assets/images/button_down@2x.png) no-repeat center center/.25rem .14rem;
	}
	
	li.is-spread>div:nth-of-type(1)::after {
		background: url(../../assets/images/button_up@2x.png) no-repeat center center/.25rem .14rem;
	}
	li.is-spread>div:nth-of-type(2){
		padding: .2rem .9rem;
		font-size: .26rem;
		line-height: .4rem;
		/*white-space: pre;*/
	}
</style>


