<template>
	<div class="payment-details">
		<ul id='isKong'>
			<li class="payment-details-item2 oh">
				<div class="total-income-text">
					<span class="total-income">收入：¥{{(have_money==null||have_money=='')?0:have_money}}嗨币 </span>
					<span class="total-income spending-left">支出：¥{{(forward_money==null||forward_money=='')?0:forward_money}}嗨币</span> 
				</div>
			</li>
			<li class="payment-details-item oh" v-for="(item, index) in paymentItems" v-if="showPic(item.status, item.type)">
					<div :class="paymentMoney(item.type)">
						<span>{{positiveSign(item.type)}}{{item.money}}</span>
					</div>
					<div class="payment-type">{{leftPic(item.status, item.type)}}</div>
					<div class="payment-text ellipsis">{{item.title}}</div>
					<div class="payment-time">{{item.time}}</div>
			</li>
			
		</ul>
		<!--<div class="no-data-block" v-if="">暂无数据...</div>-->
		<!--<div class="no-more-block" v-if="">没有更多了...</div>-->
	</div>
</template>

<script>
	import details_icon_play from '@/components/paymentDetails/images/details_icon_play@2x.png';
	import '@/assets/js/layer041002.js';
	export default {
		data: () => ({
			videoDetailsTextImgTwo: '',
			details_icon_play,
			paymentItems: [],
			exchange_icon: '',
			have_money:'',
			forward_money:'',
			
		}),

		created() {
			let _this = this;
			_this.axios({
					method: 'get',
					url: '/api/account/detail',
					responseType: 'json',
					data: {
						authToken: _this.$route.query.token,
					}
				})
				.then(function(response) {
					if(response.data.code == 200) {
						_this.exchange_icon = response.data.exchange_icon;
						_this.have_money = response.data.have;
						_this.forward_money = response.data.forward;
						
						_this.paymentItems = response.data.data;
						_this.$nextTick(() => {
							if(document.querySelectorAll('#isKong>li').length == 0) {
								layer.tipsX('没有查询记录');
							}

						});

					} else {

						layer.tipsX(response.data.message);

					}
				})
				.catch(function(error) {

					layer.tipsX(error);

				});

		},
		methods: {
			leftPic(i, t) { //控制明细类型
				if(i == 1 && t == 1) {
					return '创作';
				};
				if(t == 3) {
					return '邀请';
				};
				if(t == 2) {
					return '分享';
				};
				if(i == 1 && t == 5) {
					return '提现';
				};
				if(t == 8) {
					return '注册';
				};
			},
			showPic(i, t) { //控制需要显示的明细类型
				if(i == 1 && t == 1) {
					return true;
				};
				if(t == 3) {
					return true;
				};
				if(t == 2) {
					return true;
				};
				if(i == 1 && t == 5) {
					return true;
				};
				if(t == 8) {
					return true;
				};
				//退回支付宝 推荐位  举报视频 不显示在页面
				if(t == 7) {
					return false;
				};
				if(t == 6) {
					return false;
				};
				if(t == 4) {
					return false;
				};
			},
			paymentMoney(t) { //控制颜色
				if(t != 5) {
					return 'payment-money';
				} else {
						return 'payment-money-gray';
				};
			},
			positiveSign(i) { //控制正负号
				if(i == 5 || i == 4) {
					return "-";
				} else {
					return "+";
				};
			},
		}
	}
</script>

<style scoped>
	.payment-details {
		width: 100%;
	}
	
	.payment-details-item {
		position: relative;
		height: 1.88rem;
		background: #fff;
		margin-bottom: .1rem;
		box-sizing: border-box;
		padding-left: .5rem;
		padding-right: .5rem;
		
	}
	.payment-details-item2{
		position: relative;
		height: 1rem;
		line-height: 1rem;
		background: #fff;
		box-sizing: border-box;
		padding-left: .5rem;
		padding-right: .5rem;
		margin-top: .1rem;
		
		
	}
	.payment-time {
		float: right;
		font-size: 0.24rem;
		color: #999;
		position: absolute;
		bottom: .16rem;
		right: .5rem;
	}
	.payment-money {
		color: #FF4058;
	    font-size: 0.48rem;
	    float: right;
	    position: absolute;
	    top: .42rem;
	    right: .5rem;
	}
	
	.payment-money-gray {
		color: #999;
		font-size: 0.48rem;
	    float: right;
	    position: absolute;
	    top: .42rem;
	    right: .5rem;
	}

	.payment-text {
		font-size: 0.28rem;
		color: #999;
		margin-top: .06rem;
		width: 3.78rem;
	}
	
	.source-identification {
		font-size: 0.24rem;
		color: #999;
	}
	.payment-type{
		margin-top: .42rem;
		color: #333;
		font-size:.38rem ;
		font-weight: 600;
	}
	.total-income{
		font-size: 0.28rem;
		color: #333;
	}
	.spending-left{
		margin-left: .4rem;
	}
	
</style>


