import Vue from 'vue'
import Router from 'vue-router'
import entrance from '@/components/publicEg/entrance'
import commonQuestions from '@/components/my/commonQuestions'
import weixinDownload from '@/components/weixinDownload/weixinDownload'
import fansFollow from '@/components/my/fansFollow'
import villageFollow from '@/components/my/villageFollow'
import videoDetails from '@/components/share/videoDetails'
import myWallet from '@/components/myWallet/myWallet'
import paymentDetails from '@/components/paymentDetails/paymentDetails'
import toReport from '@/components/toReport/toReport'
import personalMainPage from '@/components/share/personalMainPage'
import inviteRegister from '@/components/share/inviteRegister'
import registrationAgreement from '@/components/registrationAgreement/registrationAgreement'
import aboutUs from '@/components/aboutUs/aboutUs'
import redPacketMain from '@/components/redPacket/redPacketMain'
import demo from '@/components/my/demo'

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			redirect: '/entrance'
		},
		{
			path: '/entrance',
			name: 'v-entrance',
			component: entrance
		},
		{
			path: '/commonQuestions',
			name: 'v-common-questions',
			component: commonQuestions
		},
		{
			path: '/weixinDownload',
			name: 'v-weixin-download',
			component: weixinDownload
		},
		{
			path: '/myFans',
			name: 'v-my-fans',
			component: fansFollow
		},
		{
			path: '/hisFans',
			name: 'v-his-fans',
			component: fansFollow
		},
		{
			path: '/hisPersonFollow',
			name: 'v-his-personfollow',
			component: fansFollow
		},
		{
			path: '/villagePersonFollow',
			name: 'v-village-personfollow',
			component: fansFollow
		},
		{
			path: '/hisVillageFollow',
			name: 'v-his-villagefollow',
			component: villageFollow
		},
		{
			path: '/videoDetails',
			name: 'v-video-details',
			component: videoDetails
		},
		{
			path: '/myWallet',
			name: 'v-my-wallet',
			component: myWallet,
		},
		{
			path: '/paymentDetails',
			name: 'v-payment-details',
			component: paymentDetails,
		},
		{
			path: '/toReport',
			name: 'v-to-report',
			component: toReport,
		},
		{
			path: '/personalMainPage',
			name: 'v-personal-main-page',
			component: personalMainPage,
		},
		{
			path: '/inviteRegister',
			name: 'v-invite-register',
			component: inviteRegister,
		},
		{
			path: '/registrationAgreement',
			name: 'v-registration-agreement',
			component: registrationAgreement,
		},
		{
			path: '/aboutUs',
			name: 'v-about-us',
			component: aboutUs,
		},
		{
			path: '/redPacketMain',
			name: 'v-red-packet-main',
			component: redPacketMain,
		},
		{
			path: '/demo',
			name: 'v-demo',
			component: demo,
		},
	]
})


