<template>
	<div>
		<main>
			<pagination-load :canload="!isStop" :distance="10" @load="getLoad">
				<ul>
					<li is="village-follow-li" v-for="(item, index) in mainDataList" :key="item.id" :data="item" :dataindex="index"></li>
				</ul>
			</pagination-load>
			<loading-gif v-show="isLoading"></loading-gif>
			<!--<div class="loading-gif-block" v-show="isLoading">正在加载...</div>-->
			<div class="no-data-block" v-if="noDataFlag">暂无数据...</div>
			<!--<div class="no-more-block" v-if="!noDataFlag&&isStop">没有更多了...</div>-->
		</main>
	</div>
</template>

<script>
	import '@/assets/js/layer041002.js';
	import paginationLoad from '@/components/publicEg/paginationLoad';
	import villageFollowLi from '@/components/my/villageFollowLi';
	export default {
		name: '',
		components: {
			'pagination-load': paginationLoad,
			'village-follow-li': villageFollowLi
		},
		data: () => ({
			mainDataList: [],
			page: 1,
			pageSize: 10,
			isLoading: false,
			isStop: false,
			noDataFlag: false
		}),
		created() {
			let _this = this;
			_this.common.duringRequest({
				'urlStr': '/api/follow/getFollowList',
				startAction() {
					_this.isLoading = true;
				},
				endAction() {
					_this.isLoading = false;
				}
			});
		},
		mounted() {
			let _this = this;
			_this.getLoad();
		},
		methods: {
			getLoad() {
				let _this = this;
				
				(!_this.isLoading&&!_this.isStop) && _this.axios({
					method: 'get',
					url: '/api/follow/getFollowList',
					responseType: 'json',
					data: {
						'authToken': _this.$route.query.token,
						'member_id': _this.$route.query.member_id===undefined?'':_this.$route.query.member_id,
						'type': 1,//他关注的小区，这里固定是1
						'pageNo': _this.page,
						'pageSize': _this.pageSize
					}
				})
				.then(function(response) {
					if(response.data.code == 200) {
						if(response.data.data){
							let _list = response.data.data.list;
							if(_list.length === 0){
								_this.page === 1 && (_this.noDataFlag = true);
								_this.isStop = true;
							}else{
								_this.mainDataList.push(..._list);//这里使用push要注意，先把数组展开
								_list.length < _this.pageSize && (_this.isStop = true);
								_this.page++;
							};
						}else{
							_this.noDataFlag = true;
							_this.isStop = true;
							layer.tipsX(response.data.message);
						};
					} else {
						layer.tipsX(response.data.message);
					}
				})
				.catch(function(error) {
					layer.tipsX(error);
				});
			}
		},
		computed: {
			
		}
	}
</script>

<style>

</style>


