<template>
	<div>
		<main>
			<pagination-load :canload="!isStop" :distance="10" @load="getLoad">
				<ul>
					<li is="fans-follow-li" v-for="(item, index) in mainDataList" :key="item.id" :data="item" :datahash="urlHash" :dataindex="index" @updateCare="updateCallBack"></li>
				</ul>
			</pagination-load>
			<loading-gif v-show="isLoading"></loading-gif>
			<!--<div class="loading-gif-block" v-show="isLoading">正在加载...</div>-->
			<div :class="'no-data-block '+noDataClass" v-if="noDataFlag">{{noDataText}}</div>
			<!--<div class="no-more-block" v-if="!noDataFlag&&isStop">没有更多了...</div>-->
		</main>
	</div>
</template>

<script>
	import '@/assets/js/layer041002.js';
	import paginationLoad from '@/components/publicEg/paginationLoad';
	import fansFollowLi from '@/components/my/fansFollowLi';
	export default {
		name: '',
		components: {
			'pagination-load': paginationLoad,
			'fans-follow-li': fansFollowLi
		},
		data() {
			let _this = this,
				_noDataText = '暂无查询结果',
				_noDataClass = '',
				_url = '',
				_hash = location.hash.slice(2).split('?')[0],
				_type = 1;
			//获取的是关注的小区的人列表
			//从url拿参数，改变title
			//不会被改变的值，可以写在data的初始化里
			
			console.log(_hash);
			if(_hash === 'hisPersonFollow' || _hash === 'fansFollow'){
				//他关注的人，
				_type = 0;
			};
			
			if(~location.hash.indexOf('villagePersonFollow')) {
				//关注小区的人
				_url = '/api/follow/getListByBuildingId';
			} else {
				_url = '/api/follow/getMyFens';
			};
			if(~location.hash.indexOf('hisPersonFollow')) {
				_noDataText = '没有关注的人';
				_noDataClass = 'no-data-block-focus-people';
			};
			return {
				ajaxUrl: _url,//请求的url
				mainDataList: [],//请求返回的数据体集合
				page: 1,//页数，每次请求完毕会增加
				pageSize: 10,//分页请求的每页数据条数
				isLoading: false,//是否在请求的过程中
				isStop: false,//是否停止分页请求
				urlHash: _hash,//
				urlDataType: _type,//
				noDataFlag: false,//是否显示暂无数据
				noDataText: _noDataText,//暂无数据的文字显示
				noDataClass: _noDataClass,//根据更改class来改变图片
			}
		},
		created() {
			let _this = this;
			_this.common.duringRequest({
				'urlStr': _this.ajaxUrl,
				startAction() {
					_this.isLoading = true;
				},
				endAction() {
					_this.isLoading = false;
				}
			});
		},
		mounted() {
			let _this = this;
			_this.getLoad();
		},
		methods: {
			updateCallBack(obj) {
				console.log(obj);
			},
			getLoad() {
				let _this = this;
				let _data = {
					'authToken': _this.$route.query.token,
					'pageNo': _this.page,
					'pageSize': _this.pageSize
				};
				if(_this.$route.query.building_id === undefined) {
					_data['member_id'] = _this.$route.query.member_id === undefined ? '' : _this.$route.query.member_id;
					_data['type'] = _this.urlDataType;
				} else {
					_data['building_id'] = _this.$route.query.building_id;
				};

				if(!_this.isLoading && !_this.isStop) {
					_this.axios({
						method: 'get',
						url: _this.ajaxUrl,
						responseType: 'json',
						data: _data
					})
					.then(function(response) {
						if(response.data.code == 200) {
							if(response.data.data) {
								let _list = response.data.data.list;
								if(_list.length === 0) {
									_this.page === 1 && (_this.noDataFlag = true);
									_this.isStop = true;
								} else {
									_this.mainDataList.push(..._list); //这里使用push要注意，先把数组展开
									_list.length < _this.pageSize && (_this.isStop = true);
									_this.page++;
								};
							} else {
								_this.noDataFlag = true;
								_this.isStop = true;
								layer.tipsX(response.data.message);
							};
						} else {
							layer.tipsX(response.data.message);
						};
					})
					.catch(function(error) {
						layer.tipsX(error);
					});
				};
			}
		},
		computed: {

		}
	}
</script>

<style>

</style>


