<template>
	<li data-id="data.id">
		<div class="content-area flex">
			<div class="flex-center">
				<img-error :datasrc="data.headimgurl"></img-error>
			</div>
			<div class="flex">
				<p>{{data.nickname}}</p>
				<p>{{dealTime}}</p>
			</div>
			<div class="flex-center">解锁{{data.money}}元</div>
		</div>
	</li>
</template>

<script>
	import '@/assets/js/layer041002.js';
	export default {
		name: '',
		props: {
			data: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			},
			dataindex: {
				type: [Number, String],
				default: 0
			},
			urltype: {
				type: String,
				default: 'lookat'
			}
		},
		components: {
			
		},
		data: () => ({
			
		}),
		created() {
			let _this = this;
			
		},
		mounted() {
			let _this = this;
			
		},
		methods: {
			
		},
		computed: {
			dealTime() {
				let _this = this;
				return _this.common.timestampToTime(_this.data.create_time);
			}
		}
	}
</script>

<style scoped>
	li+li{
		padding-top: .1rem;
	}
	.content-area>div:nth-of-type(1){
		flex: .8rem 0 0;
		padding-right: .18rem;
	}
	.content-area>div:nth-of-type(1)>img{
		width: .8rem;
		height: .8rem;
		border-radius: 50%;
	}
	.content-area>div:nth-of-type(2){
		flex: .28rem;
		flex-direction: column;
		justify-content: center;
	}
	.content-area>div:nth-of-type(2)>p:nth-of-type(1){
		font-size: .28rem;
		color: #666;
	}
	.content-area>div:nth-of-type(2)>p:nth-of-type(2){
		font-size: .24rem;
		color: #999;
	}
	.content-area>div:nth-of-type(3){
		font-size: .32rem;
		color: #666;
	}
</style>


