<template>
	<main>
		<div class="sec-one">
			<div class="head-area por">
				<div class="flex-center poa">
					<div class="flex-center"><img-error :datasrc="image_url" :imgtype="'div'"></img-error></div>
				</div>
			</div>
			<div class="head-txt-area tc">
				<p>{{packet_sender}}</p>
				<p>{{package_notes}}</p>
				<p><span>{{amount}}</span>元</p>
				<p>已解锁<span>{{total_money}}</span>元,还差<span>{{surplus_money}}</span>元</p>
			</div>
			<div class="progress-area flex-center">
				<div class="por">
					<span></span>
					<span :style="'width: '+rate+'%'"></span>
					<img :style="'left: '+rate+'%'" :src="goldCionIcon" />
				</div>
			</div>
		</div>
		<div class="sec-two flex-center">
			<a href="javascript:;" class="flex-center" @click="shareApp">邀好友帮你解锁</a>
		</div>
		<div class="sec-three" v-if="mainDataList.length>0">
			<div>
				<div>
					<underline-block>
						<p class="sec-title">已邀好友({{mainDataList.length}})</p>
					</underline-block>
					<ul>
						<li is="self-defined-li" v-for="(item, index) in mainDataList" :data="item" :dataindex="index"></li>
					</ul>
				</div>
			</div>
		</div>
		<div class="sec-four">
			<div>
				<div>
					<underline-block>
						<p class="sec-title">提现秘方</p>
					</underline-block>
					<ul>
						<li>1、点击邀好友帮你解锁</li>
						<li>2、好友下载APP-点击发布按钮-填写邀请码</li>
						<li>3、你就可以去我的钱包提现到支付宝啦</li>
					</ul>
				</div>
			</div>
		</div>
	</main>
</template>

<script>
	import '@/assets/js/layer041002.js';
	import underlineBlock from '@/components/publicEg/underlineBlock';
	import selfDefinedLi from '@/components/redPacket/redPacketLi';
	import goldCionIcon from '@/components/redPacket/images/gold_coin@2x.png';
	export default {
		name: '',
		props: {
			data: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			},
			dataindex: {
				type: [Number, String],
				default: 0
			}
		},
		components: {
			'underline-block': underlineBlock,
			'self-defined-li': selfDefinedLi
		},
		data() {
			let _this = this;
			return {
				token: _this.$route.query.token,
				member_id: _this.$route.query.member_id,
				goldCionIcon,
				image_url: '',
				packet_sender: '',
				package_notes: '',
				amount: 0,//总数
				total_money: 0,//已解锁的部分
				surplus_money: 0,//未解锁的部分
				mainDataList: []
			}
		},
		created() {
			let _this = this;
			_this.getInfo();
		},
		mounted() {
			let _this = this;
			
		},
		methods: {
			getInfo() {
				let _this = this;
				_this.axios({
					method: 'get',
					url: '/api/invite/redPacketDetail',
					responseType: 'json',
					data: {
						'authToken': _this.token,
						'member_id': _this.member_id
					}
				})
				.then(function(response) {
					if(response.data.code == 200) {
						let _data = response.data;
						_this.image_url = _data.image_url;
						_this.packet_sender = _data.packet_sender;
						_this.package_notes = _data.package_notes;
						_this.total_money = _data.total_money;
						
						//_this.amount = _data.amount;
						//_this.surplus_money = _data.surplus_money;
						_this.amount = 88;//临时改成写死的值
						_this.surplus_money = 88-_data.total_money;
						
						if(_data.data){
							_this.mainDataList = _data.data;
						};
					} else {
						layer.tipsX(response.data.message);
					};
				})
				.catch(function(error) {
					layer.tipsX(error);
				});
			},
			shareApp() {
				let _this = this;
				_this.common.Compatible({
					paraIos: {
						'route': 'openShareDialog'
					},
					fnAndroid() {
						window.android.openShareDialog();
					},
					fn() {
						console.warn('error');
					}
				});
			}
		},
		computed: {
			rate() {
				let _this = this;
				if(_this.total_money){
					return parseInt(_this.total_money/_this.amount*100);
				}else{
					//当total_money为0时，直接返回0
					return 0;
				};
			}
		}
	}
</script>

<style scoped>
	main{
		background-color: white;
	}
	/*头像部分*/
	.head-area{
		height: 2.58rem;
		background: url(images/BG2@2x.png) no-repeat center top/7.5rem 1.88rem;
	}
	.head-area>div{
		bottom: 0;
		left: 0;
		width: 100%;
	}
	.head-area>div>div{
		width: 1.6rem;
		height: 1.6rem;
		border-radius: 50%;
		border: .03rem solid white;
		background-color: white;
	}
	.head-area>div>div>img{
		width: 1.6rem;
		height: 1.6rem;
		border-radius: 50%;
	}
	
	/*头向下的文字部分*/
	.head-txt-area>p:nth-of-type(1){
		font-size: .4rem;
		color: #333;
		padding-top: .5rem;
	}
	.head-txt-area>p:nth-of-type(2){
		font-size: .32rem;
		color: #999;
		padding-top: .1rem;
	}
	.head-txt-area>p:nth-of-type(3){
		font-size: .4rem;
		color: #2a2a2a;
		padding-top: .18rem;
	}
	.head-txt-area>p:nth-of-type(3)>span{
		color: #CE3A3E;
		font-size: 1.04rem;
	}
	.head-txt-area>p:nth-of-type(4){
		color: #999;
		font-size: .36rem;
		padding-top: .18rem;
	}
	.head-txt-area>p:nth-of-type(4)>span{
		color: #CE3A3E;
	}
	
	/*进度条部分*/
	.progress-area{
		padding: .28rem 0 .3rem;
	}
	.progress-area>div{
		width: 5.4rem;
		height: .4rem;
	}
	.progress-area>div>span{
		width: 100%;
		height: .06rem;
		position: absolute;
		left: 0;
		top: 50%;
		border-radius: .03rem;
		margin-bottom: -.03rem;
	}
	.progress-area>div>span:nth-of-type(1){
		background-color: #f1f1f1;
	}
	.progress-area>div>span:nth-of-type(2){
		background-color: #659CFF;
	}
	.progress-area>div>img{
		position: absolute;
		top: 0;
		width: .4rem;
		height: .4rem;
		margin-left: -.2rem;
	}
	
	/*按钮区域*/
	.sec-two{
		padding-bottom: .6rem;
	}
	.sec-two>a{
		width: 4.76rem;
		height: 1.02rem;
		border-radius: .05rem;
		background-color: #659CFF;
		font-size: .32rem;
		color: white;
	}
	
	/**/
	.sec-three{
		padding: 0 .56rem .2rem;
	}
	.sec-four{
		padding: 0 .56rem .5rem;
	}
	.sec-three>div,
	.sec-four>div{
		background-color: rgba(101, 156, 255, .05);
		padding: .2rem .4rem;
		border-radius: .05rem;
	}
	.sec-title{
		padding: 0 .5em;
	}
	
	.sec-four ul{
		padding-top: .46rem;
		padding-bottom: .68rem;
	}
	.sec-four li{
		font-size: .24rem;
		color: #2a2a2a;
		line-height: .56rem;
		height: .56rem;
	}
</style>


