<template>
	<div>
		<div class="head-area flex">
			<div class="head-avatar-area">
				<img-error :datasrc="headimgurl"></img-error>
			</div>
			<div class="head-txt-area">
				<p>{{nickname}}</p>
				<p>嗨房ID:{{hiid}}</p>
				<p>{{sign}}</p>
				<p class="oh"><span :class="'x-span fl '+dealSex">22</span></p>
			</div>
		</div>
		<div class="number-area flex">
			<div>
				<p>{{num_buildings}}</p>
				<p>小区</p>
			</div>
			<div>
				<p>{{num_prise}}</p>
				<p>获赞</p>
			</div>
			<div>
				<p>{{num_member}}</p>
				<p>关注</p>
			</div>
			<div>
				<p>{{num_follow}}</p>
				<p>粉丝</p>
			</div>
		</div>
		<div class="nav-tab-area flex">
			<div v-for="(item, index) in mainData" :key="index" class="pointer-click-item flex-center por" :class="{active:index === initTabNumMain}" @click="tabMain(index)">{{item.title}}</div>
		</div>
		<main>
			<section v-for="(item, index) in mainData" v-show="index === initTabNumMain">
				<pagination-load :canload="!item.isStop" :distance="30" @load="getList">
					<ul class="house-resources-ul">
						<li is="self-defined-li" v-for="(item2, index2) in item.dataList" :data="item2" :dataindex="index2" @click.native="clickVideoResources(item2.id)"></li>
					</ul>
				</pagination-load>
				<div class="no-data-block no-data-block-video no-data-block-video-sp" v-show="item.noDataFlag">暂无房源视频</div>
				<div class="no-more-block" v-show="!item.noDataFlag&&item.isStop">没有更多了...</div>
			</section>
			<loading-gif v-show="isLoading"></loading-gif>
		</main>
	</div>
</template>

<script>
	import '@/assets/js/layer041002.js';
	import paginationLoad from '@/components/publicEg/paginationLoad';
	import selfDefinedLi from '@/components/publicEg/houseResourcesLi';
	export default {
		name: '',
		props: {
			data: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			},
			dataindex: {
				type: [Number, String],
				default: 0
			}
		},
		components: {
			'pagination-load': paginationLoad,
			'self-defined-li': selfDefinedLi
		},
		data() {
			let _this = this;
			let applogin_id = _this.$route.query.applogin_id;//app登录用户的id
			let member_id = _this.$route.query.member_id;//查看的这个人的id
			return {
				applogin_id,//app登录用户的id
				member_id,//查看的这个人的id
				headimgurl: '',//头像
				nickname: '',//昵称
				hiid: '',//嗨房id
				sign: '',//个性签名
				sex: 1,//性别1男，2女
				num_buildings: 0,//小区数
				num_prise: 0,//获赞数
				num_member: 0,//关注的人数
				num_follow: 0,//粉丝数
				pageSize: 10,
				initTabNumMain: 0,
				isLoading: false,//是否正在加载
				mainData: [{
					'title': '出售房源',
					'page': 1,//页码
					'isLoadOnce': false,//是否请求过一次数据
					'isStop': false,//是否所有页的数据加载完毕
					'noDataFlag': false,//是否是无数据
					'dataList': []
				},
				{
					'title': '出租房源',
					'page': 1,//页码
					'isLoadOnce': false,//是否请求过一次数据
					'isStop': false,//是否所有页的数据加载完毕
					'noDataFlag': false,//是否是无数据
					'dataList': []
				},
				{
					'title': 'TA赞过的',
					'page': 1,//页码
					'isLoadOnce': false,//是否请求过一次数据
					'isStop': false,//是否所有页的数据加载完毕
					'noDataFlag': false,//是否是无数据
					'dataList': []
				}]
			}
		},
		created() {
			let _this = this;
			_this.common.duringRequest({
				'urlStr': '/api/video/getVideoByMemberId',
				startAction() {
					_this.isLoading = true;
				},
				endAction() {
					_this.isLoading = false;
				}
			},{
				'urlStr': '/api/follow/getFollowListByMemberId',
				startAction() {
					_this.isLoading = true;
				},
				endAction() {
					_this.isLoading = false;
				}
			});
			
			_this.axios({
				method: 'get',
				url: '/api/member/getMemberDetail',
				responseType: 'json',
				data: {
					'member_id': _this.member_id
				}
			})
			.then(function(response) {
				if(response.data.code === 200 && response.data.data) {
					let _data = response.data.data;
					_this.headimgurl = _data.headimgurl;
					_this.nickname = _data.nickname;
					_this.hiid = _data.hiid;
					_this.sign = _data.sign?_data.sign:_this.common.defaultSign;
					_this.sex = _data.sex;
					_this.mainData[2].title = _this.sex==1?'他赞过的':'她赞过的'
					_this.num_buildings = _data.num_buildings;
					_this.num_prise = _data.num_prise;
					_this.num_member = _data.num_member;
					_this.num_follow = _data.num_follow;
				} else {
					layer.tipsX(response.data.message);
				}
			})
			.catch(function(error) {
				layer.tipsX(error);
			});
			
			_this.loadMain();
		},
		methods: {
			loadMain() {
				let _this = this;
				_this.getList();
				_this.common.userBehavior({
					'id': _this.applogin_id,
					'type': 21
				});
			},
			tabMain(index) {
				let _this = this;
				_this.initTabNumMain = index;
				if(!_this.mainData[index].isLoadOnce) {
					_this.getList();
				};
			},
			getList() {
				let _this = this;
				let _index = _this.initTabNumMain;
				if(!_this.isLoading && !_this.mainData[_index].isStop) {
					let _data = {
						'pageNo': _this.mainData[_index].page,
						'pageSize': _this.pageSize,
						'member_id': _this.member_id,
						
					};
					if(_index !== 2){
						_data.type = _index==0?1:0;
					};
					_this.axios({
						method: 'get',
						url: _index !== 2?'/api/video/getVideoByMemberId':'/api/follow/getFollowListByMemberId',
						responseType: 'json',
						data: _data
					})
					.then(function(response) {
						_this.mainData[_index].isLoadOnce = true;
						if(response.data.code == 200) {
							let _rdata = response.data.data;
							if(_rdata && _rdata.list && Array.isArray(_rdata.list) && _rdata.list.length>0){
								let _list = response.data.data.list;
								_this.mainData[_index].dataList.push(..._list); //这里使用push要注意，先把数组展开
								_list.length < _this.pageSize && (_this.mainData[_index].isStop = true);
								_this.mainData[_index].page += 1;
							}else{
								_this.mainData[_index].page === 1 && (_this.mainData[_index].noDataFlag = true);
								_this.mainData[_index].isStop = true;
							}
							
						} else {
							layer.tipsX(response.data.msg);
						}
					})
					.catch(function(error) {
						layer.tipsX(error);
					});
				};
			},
			clickVideoResources(id) {
				let _this = this;
				_this.common.userBehavior({
					'id': _this.applogin_id,
					'type': 26
				},() => {
					_this.$router.push({
						path: '/videoDetails',
						query: {
							'member_id': _this.applogin_id,
							'video_id': id
						}
					});
				});
			}
		},
		computed: {
			dealSex() {
				let _this = this;
				return _this.sex == 1?'male-span':'female-span';
			}
		}
	}
</script>

<style scoped>
	/*头部基本信息区域*/
	.head-area{
		background-color: white;
		height: 2.1rem;
		padding-top: .32rem;
		padding-bottom: .32rem;
	}
	.head-avatar-area{
		flex: 2.66rem 0 0;
		box-sizing: border-box;
		padding: 0 .54rem 0 .36rem;
	}
	.head-avatar-area>img{
		width: 1.76rem;
		height: 100%;
	}
	.head-txt-area{
		flex: 4.84rem 0 0;
	}
	.head-txt-area>p:nth-of-type(1){
		font-size: .56rem;
	}
	.head-txt-area>p:nth-of-type(2){
		font-size: .28rem;
		color: #333;
		padding-top: .06rem;
	}
	.head-txt-area>p:nth-of-type(3){
		font-size: .24rem;
		color: #999;
		padding-top: .14rem;
	}
	.head-txt-area>p:nth-of-type(4){
		padding-top: .16rem;
		font-size: .18rem;
		height: .3rem;
		color: white;
		line-height: .3rem;
	}
	
	.x-span{
		border-radius: .15rem;
		padding-left: .4rem;
		padding-right: .15rem;
	}
	.male-span{
		/*男*/
		background: #50E3C2 url(images/icon_male@2x.png) no-repeat .15rem center/.2rem .2rem;
	}
	.female-span{
		/*女*/
		background: #FF8897 url(images/icon_female@2x.png) no-repeat .15rem center/.2rem .2rem;
	}
	
	/*数字区域，小区，获赞数等*/
	.number-area{
		background-color: white;
		height: 1.4rem;
	}
	.number-area>div{
		flex: 1;
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: center;
	}
	.number-area>div>p:nth-of-type(1){
		color: #333;
		font-size: .36rem;
		font-weight: 600;
	}
	.number-area>div>p:nth-of-type(2){
		color: #666;
		font-size: .24rem;
	}
	
	/*导航栏tab切换区域*/
	.nav-tab-area>div{
		flex: 1 0 0;
		background-color: white;
		height: .98rem;
		margin-top: .2rem;
		font-size: .32rem;
		color: #333;
		flex-flow: 600;
	}
	.nav-tab-area>div.active::after{
		content: '';
		position: absolute;
		left: 50%;
		bottom: 0;
		width: .6rem;
		margin-left: -.3rem;
		height: .08rem;
		border-radius: .04rem;
		background-color: #659CFF;
	}
</style>


