<template>
	<div class="downCon">
		<div class="download_bg por">
			<img class="poa" :src="wechatPublicNumberQr" />
		</div>
		<a href="javascript:;" class="flex-center" id="downloadNow" @click.prevent.stop="download">立即下载</a>
		<div class="weixin-download" v-show="layerWeiXin"><img :src="weixinDownloadPic" alt="" class="floating-layer" /></div>

	</div>
</template>

<script>
	import weixinDownloadPic from '@/components/weixinDownload/images/download_weixin_float.png';
	import bgImage from '@/components/weixinDownload/images/download_bg@2x.png';
	import wechatPublicNumberQr from '@/assets/images/wechat_public_number_qr.jpg';
	export default {
		data: () => ({
			downloadAddress: '',
			weixinDownloadPic,
			wechatPublicNumberQr,
			layerWeiXin: false,
		}),
		created() {
			let _this = this;
			if(_this.common.isIos()) {
				_this.downloadAddress = 'https://itunes.apple.com/cn/app/id1383123091';
				location.href = _this.downloadAddress; //直接进入下载页
			} else {
				//获取安卓下载链接，type为1,苹果为2
				_this.axios({
						method: 'get',
						url: '/api/version/getVersionNo',
						responseType: 'json',
						data: {
							'type': 1
						}
					})
					.then(function(response) {
						if(response.data.code == 200) {
							_this.downloadAddress = response.data.data.app_path;
						} else {
							layer.tipsX(response.data.msg);
						}
					})
					.catch(function(error) {
						layer.tipsX(error);

					});
			}
		},
		methods: {
			download() {
				let _this = this;
				if(_this.common.isIos()) {
					location.href = _this.downloadAddress; //直接进入下载页
				} else {
					if(_this.common.isWeixinBrowser()) {
						_this.layerWeiXin = true;
					} else {
						location.href = _this.downloadAddress;
					}
				}
			}
		}

	}
</script>

<style scoped>
	.downCon {
		width: 100%;
	}
	
	.download_bg {
		width: 100%;
		height: 58.64rem;
		background: url(images/download_bg@2x.png) no-repeat 0 0/100% 58.64rem;
	}
	
	.download_bg>img {
		width: 2.58rem;
		height: 2.58rem;
		left: 50%;
		bottom: 2.66rem;
		margin-left: -1.29rem;
	}
	
	#downloadNow {
		width: 3.6rem;
		height: .68rem;
		border-radius: .34rem;
		color: white;
		font-size: 0.28rem;
		background-color: #147BFB;
		letter-spacing: 0.21px;
		position: fixed;
		bottom: 1.24rem;
		left: 50%;
		margin-left: -1.8rem;
		box-shadow: 0 0 .5rem rgba(0, 18, 98, .3);
	}
	
	.weixin-download {
		position: fixed;
		left: 0px;
		right: 0px;
		bottom: 0px;
		top: 0px;
		background: rgba(34, 34, 34, 0.85) none repeat scroll 0% 0%;
		z-index: 1000;
	}
	
	.floating-layer {
		width: 5.5rem;
		margin-right: 0.3rem;
		float: right;
	}
</style>


