<template>
	<div class="flex underline-area">
		<div class="flex-center">
			<div class="underline-div"></div>
		</div>
		<div class="flex-center">
			<slot></slot>
		</div>
		<div class="flex-center">
			<div class="underline-div"></div>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			
		},
		data() {
			let _this = this;
			return {
				type: 'hello'
			};
		}
	}
</script>

<style>
	.underline-area>div:nth-of-type(1),
	.underline-area>div:nth-of-type(3){
		flex: 1 0 0;
	}
	.underline-area>div:nth-of-type(2){
		font-size: .32rem;
		color: #666;
	}
	.underline-div{
		width: 100%;
		height: 1px;
		background-color: #979797;
	}
</style>


