<template>
	<div>
		<download-float :data="shareHeaderData" :class="{'normal-bg':haveClick}"></download-float>
		<div class="video-area" :class="{'normal-height':haveClick}">
			<!--:poster="videoCover" -->
			<!--取消视频封面的展示-->
			<video id="video_main" :src="videoLink" @click="pausePlayPar($event)" @playing="startPlay" @ended="endPlay" preload="auto" type="video/mp4" width="100%" webkit-playsinline="true" playsinline="true" x5-video-player-type="h5" x5-video-player-fullscreen="portraint"></video>
			<img :src="buttonPlay" alt="" class="stop-play" v-show="isPause" @click="pausePlayPar($event)" />
			<div class="video-text-area bsb" @click="goPage">
				<div class="house-info-div"><span>{{videoRoom+'室'+videoHall+'厅/'+videoArea+'m²/'}}</span><span>{{videoPrice}}{{unit}}</span></div>
				<div class="oh"><span class="fl">{{videoName}}</span></div>
				<div class="average-price">均价：{{videoaVeragePrice}}元/m²</div>
				<div>{{videoTitle}}</div>
				<figure class="video-avatar-area">
					<div class="por">
						<img-error :datasrc="videoUserAvatar"></img-error>
						<div class="add-icon poa"></div>
					</div>
				</figure>
			</div>
			
			<div class="share-video-other">
				<h2 class="other-house">- TA的其他房源 -</h2>
				<div class="other-item">
					<ul class="house-resources-ul">
						<li v-for="item in items" :data="item" is="house-resources-li" @click.native="clickVideoResources"></li>
					</ul>
				</div>
			</div>
		</div>
		
		<!--<div class="share-video-other">
			<h2 class="other-house">- TA的其他房源 -</h2>
			<div class="other-item">
				<ul class="flex house-resources-ul" @click="goPage">
					<li v-for="item in items" :data="item" is="house-resources-li"></li>
				</ul>
			</div>
		</div>-->

	</div>
</template>

<script>
	import downloadFloat from '@/components/publicEg/downloadFloatTop';
	import buttonPlay from '@/components/share/images/button_play@2x.png';
	import houseResourcesLi from '@/components/publicEg/houseResourcesLi';

	export default {
		data() {
			let _this = this;
			return {
				member_id: _this.$route.query.member_id,//分享此链接的用户id
				buttonPlay,//暂停按钮的图片
				isPause: true,//是否处于暂停状态
				haveClick: false,//是否点击播放过视频
				videoUserAvatar: '',//video旁边的用户头像
				videoRoom: '',//室数
				videoHall: '',//厅数
				videoArea: '',//楼盘位置
				videoPrice: '',//价格
				unit: '',//单位
				videoName: '',//楼盘名字
				videoTitle: '',//楼盘介绍文字
				videoaVeragePrice: '',//均价
				videoLink: '',//楼盘视频链接
				videoCover: '',//视频封面
				items: [],
				shareHeaderData: {
					headimgurl: '',
					nickname: ''
				}
			}
		},
		components: {
			'download-float': downloadFloat,
			'house-resources-li': houseResourcesLi
		},

		created() {
			let _this = this;
			_this.getVideo();
			_this.videoUserEvent(14);
//			if(_this.common.isWeixinBrowser()){
//				//如果是微信浏览器，则需要先授权，再调用红包接口
//				_this.authorize((obj) => {
//					console.log(obj);
//					_this.getVideo(() => {
//						_this.getRedPacketInfo(obj);
//					});
//				});
//			}else{
//				//如果不是微信浏览器，则直接播放视频
//				_this.getVideo();
//			}
			
		},
		methods: {
			startPlay() {
				let _this = this;
				console.log('play enter');
				_this.videoUserEvent(15);
			},
			endPlay() {
				let _this = this;
				console.log('play complete');
				_this.videoUserEvent(16);
			},
			videoUserEvent(type, fn) {
				let _this = this;
				_this.common.userBehavior({
					'id': _this.$route.query.member_id,
					'video_id': _this.$route.query.video_id,
					'type': type
				},fn);
			},
			//微信端网页授权，暂不用
			authorize(fn) {
				let _this = this;
				_this.axios({
					method: 'get',
					url: 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxfb04e0c5fd994aa9&redirect_uri=https%3A%2F%2Fyftest.fujuhaofang.com%2Fapi%2Fwxpublic%2Fgetuserinfo&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect',
					responseType: 'json'
				})
				.then(function(response) {
					console.log(response);
					console.log(response.data);
					if(response.data.code === 200 && response.data.data) {
						fn && fn(response.data.data);
					} else {
						layer.tipsX(response.data.message);
					}
				})
				.catch(function(error) {
					layer.tipsX(error);
				});
			},
			//获取红包相关信息接口，暂不用
			getRedPacketInfo(obj) {
				let _this = this;
				_this.axios({
					method: 'post',
					url: '/api/video/shareVideo',
					responseType: 'json',
					data: {
						'unionid': obj.unionid,
						'website': ''
					}
				})
				.then(function(response) {
					if(response.data.code === 200 && response.data.data) {
						layer.tipsX('success red packet');
						layer.tipsX(response.data.message);
					} else {
						layer.tipsX(response.data.message);
					}
				})
				.catch(function(error) {
					layer.tipsX(error);
				});
			},
			//获取视频相关链接等
			getVideo(fn) {
				let _this = this;
				_this.axios({
					method: 'get',
					url: '/api/video/getWXVideoDetail',
					responseType: 'json',
					data: {
						'video_id': _this.$route.query.video_id,
						'member_id': _this.$route.query.member_id
					}
				})
				.then(function(response) {
					if(response.data.code === 200 && response.data.data) {
						_this.items = response.data.data.list;
						_this.shareHeaderData = response.data.data.member;
						let _videoData = response.data.data.video;
						_this.videoRoom = _this.common.transCn(_videoData.num_room);
						_this.videoHall = _this.common.transCn(_videoData.num_hall);
						_this.videoArea = _videoData.area;
						_this.videoPrice = _videoData.price;
						_this.unit = (_videoData.video_type == '1')?'万':'元/月';
						_this.videoName = _videoData.name;
						_this.videoTitle = _videoData.title;
						_this.videoaVeragePrice = parseInt(_videoData.average_price);
						_this.videoLink = _videoData.video_link;
						_this.videoCover = _videoData.video_cover;
						_this.videoUserAvatar = _videoData.headimgurl;
						fn && fn();
					} else {
						layer.tipsX(response.data.message);
					}
				})
				.catch(function(error) {
					layer.tipsX(error);
				});
			},
			pausePlayPar(e) { //作用于vedio的父元素
				e.stopPropagation();
				let _this = this;
				let _videoDom = document.getElementById('video_main') && e.target.parentNode.childNodes[0];//获取video的dom节点
				if(_this.isPause) {
					_videoDom.play();
				} else {
					_videoDom.pause();
				};
				!_this.haveClick && (_this.haveClick = true);//点击一次后就标识为已点击过
				_this.isPause = !_this.isPause;
			},
			goPage() {
				let _this = this;
				if(_this.common.isAndroid()){
					location.href = 'hifang://admin.hifang.com/splash';
				}else{
					location.href = 'applinks:fujuhaofang.com/splash';
				};
				
				let t = Date.now();
				setTimeout(function() {
					if(Date.now() - t < 1200) {
						_this.$router.push({path: '/weixinDownload'});
					}
				}, 1200);
			},
			clickVideoResources() {
				let _this = this;
				_this.common.userBehavior({
					'id': _this.member_id,
					'type': 26
				},() => {
					_this.$router.push({path: '/weixinDownload'});
				});
			}
		}
	}
</script>

<style scoped>
	.normal-bg{
		background-color: rgba(255,255,255,.6);
	}
	.video-area {
		position: relative;
		height: calc(100vh - 3.5rem);
		/*overflow: hidden;*/
	}
	.video-area.normal-height{
		height: 100%;
		max-height: 100vh;
	}
	.video-box{
		position: relative;
		z-index: 1;
	}
	.video-area>video {
		width: 7.5rem;
		object-fit: cover;
	}
	
	.stop-play {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		width: 1.4rem;
		height: 1.4rem;
	}
	
	.video-text-area {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		min-height: 2.17rem;
		/*background: rgba(0,0,0,.2);*/
		padding: .3rem;
	}
	.video-text-area>div{
		font-size: .24rem;
		color: #fff;
	}
	.average-price{
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	.video-text-area>div:nth-of-type(2){
		position: relative;
		line-height: .6rem;
		font-size: .32rem;
		padding: .05rem 0 .1rem;
	}
	.video-text-area>div:nth-of-type(2)::before,
	.video-text-area>div:nth-of-type(2)::after{
		content: '';
		float: left;
		height: .6rem;
	}
	.video-text-area>div:nth-of-type(2)::before{
		width: .22rem;
		padding-right: .1rem;
		background: url(images/home_icon_coordinates@2x.png) no-repeat center center/.22rem .27rem;
	}
	.video-text-area>div:nth-of-type(2)::after{
		width: .4rem;
		padding-left: .15rem;
		background: url(images/home_icon_add@2x.png) no-repeat center center/.4rem .4rem;
	}
	
	.video-avatar-area{
		position: absolute;
		right: .3rem;
		top: .3rem;
		width: .97rem;
		height: .97rem;
	}
	.video-avatar-area>div>img{
		width: .97rem;
		height: .97rem;
		box-sizing: border-box;
		border-radius: 50%;
		border: 1px solid #fff;
	}
	.video-avatar-area>div>.add-icon{
		left: 50%;
		bottom: -.2rem;
		width: .4rem;
		margin-left: -.2rem;
		height: .4rem;
		background: url(images/home_icon_add@2x.png) no-repeat center center/.4rem .4rem;
	}
	
	.share-video-other{
		position: absolute;
		top: 100%;
		left: 0;
		background-color: white;
	    min-height: 3.5rem;
	    width: 100%;
	}
	
	.other-house {
		text-align: center;
		font-size: .28rem;
		color: #1C1C1C;
		height: 1.2rem;
		line-height: 1.2rem;
	}
	
	.house-info-div{
		overflow: hidden;
	}
	.house-info-div>span{
		float: left;
		height: .4rem;
		line-height: .4rem;
		font-size: .24rem;
		padding: 0 .2em;
		border-radius: .04rem;
		padding-left: .4rem;
	}
	.house-info-div>span:nth-of-type(1){
		background: rgba(0,0,0,.7) url(images/home_icon_1.png) no-repeat .05rem 40%/.3rem .3rem;
	}
	.house-info-div>span:nth-of-type(2){
		margin-left: .1rem;
		background: rgba(0,0,0,.7) url(images/home_icon_2.png) no-repeat .05rem 40%/.3rem .3rem;
	}
</style>


