<template>
	<div class="loadmore" ref="loadmore">
		<div class="loadmore__body">
			<slot></slot>
		</div>
		<div class="loadmore__footer">
			<!--<span v-if="stop"><i class="tc-loading"></i><span>正在加载...</span></span>-->
			<!--<span v-else-if="loading">正在加载...</span>-->
			<!--<span v-else>没有更多了</span>-->
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			/**
			 * 是否加载
			 */
			canload: {
				type: Boolean,
				default: true,
			},

			/**
			 * 滚动外部容器, 选择器字符串
			 */
			container: {
				default: () => (global),
			},

			/**
			 * 距离底部多远加载
			 */
			distance: {
				type: Number,
				default: 100,
			},
		},

		data() {
			return {
				dom: null, // 外部容器dom
			}
		},
		mounted() {
			if(this.container !== global) {
				this.dom = document.querySelector(this.container)
			} else {
				this.dom = this.container
			}
			if(!this.dom) {
				return
			}
			this.dom.addEventListener('scroll', this.scroll, false)
		},

		methods: {
			/**
			 * 滚动钩子
			 */
			scroll() {
				if(!this.canload) {
					return
				}
				const viewHeight = global.innerHeight
				let parentNode
				if(this.container !== global) {
					parentNode = this.$el
				} else {
					parentNode = this.$el.parentNode
				}
				if(parentNode) {
					//const rect = parentNode.getBoundingClientRect();
					//if((rect.bottom <= viewHeight + this.distance)) {
					if((this.getScrollTop() + this.getClientHeight() + this.distance >= this.getScrollHeight())) {
						//DOM 更新循环结束之后执行
						this.$nextTick(() => {
							this.load()
						})
					}
				}
			},
			//获取滚动条当前的位置 
			getScrollTop() {
				let scrollTop = 0;
				if(document.documentElement && document.documentElement.scrollTop) {
					scrollTop = document.documentElement.scrollTop;
				} else if(document.body) {
					scrollTop = document.body.scrollTop;
				}
				return scrollTop;
			},
			//获取当前可视范围的高度
			getClientHeight() {
				let clientHeight = 0;
				if(document.body.clientHeight && document.documentElement.clientHeight) {
					clientHeight = Math.min(document.body.clientHeight, document.documentElement.clientHeight);
				} else {
					clientHeight = Math.max(document.body.clientHeight, document.documentElement.clientHeight);
				}
				return clientHeight;
			},
			//获取文档完整的高度
			getScrollHeight() {
				return Math.max(document.body.scrollHeight, document.documentElement.scrollHeight);
			},
			/**
			 * 加载一组数据的方法
			 */
			load() {
				this.$emit('load')
			},
		},
		beforeDestroy() {
			if(this.dom) {
				this.dom.removeEventListener('scroll', this.scroll, false)
			}
		},
	}
</script>
<style>

</style>


