<template>
			<li :class="{'to-report':isSelected}" @click="isSelect">
				<span class="to-report-text">{{data.content}}</span>
				<span class="to-report-pic" ></span>
			</li>
</template>

<script>
	export default {
		name: '',
		props: {
			data: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			},
			dataindex: {
				type: [Number, String],
				default: 0
			}
		},
		data: () => ({
			isSelected:false,
			
		}),
		created() {
			

		},
		methods:{
            
			isSelect(){
			let _this = this;
			_this.isSelected=!_this.isSelected;
			_this.$emit('report',{
				'id': _this.data.id,
				'isSelected': _this.isSelected,
			});
			
			}

		}

	}
</script>

<style scoped>
	li{
		box-sizing: border-box;
		width: 100%;
		height: 1.2rem;
		line-height: 1.2rem;
		background: #fff;
		margin-top: .04rem;
		font-size: .28rem;
		color:#666;
		
	}
	.to-report-text{
		margin-left: .38rem;
		
	}
	li>.to-report-pic{
		float: right;
		width: .48rem;
		height: .48rem;	
		margin: .4rem .4rem 0 0;
		background: url(images/icon_unselected@2x.png) no-repeat center center/100% 100%;
	}
	li.to-report>.to-report-pic{
		float: right;
		width: .48rem;
		height: .48rem;	
		margin: .4rem .4rem 0 0;
		background: url(images/icon_selected@2x.png) no-repeat center center/100% 100%;
	}

	.tijiao{
		width: 100%;
		height: 1rem;
		background: #659CFF;
		font-size: .38rem;
		color: #FFF;
		text-align: center;
		line-height: 1rem;
		position: fixed;
		bottom: 0;
		left: 0;
	}
	.hide-text{
		visibility: hidden;
	}
	
</style>


