<template>
	<li class="flex">
		<div class="flex" @click="goPage">
			<img-error :datasrc="data.headimgurl"></img-error>
		</div>
		<div class="flex" @click="goPage">
			<div>
				<span class="ellipsis">{{data.nickname?data.nickname:'暂无'}}</span>
				<!--标签这个版本先不做-->
				<!--<mark :class="typeClass">{{data.label_name}}</mark>-->
			</div>
			<div class="ellipsis">{{data.sign?data.sign:'看房小能手'}}</div>
		</div>
		<div class="flex">
			<a href="javascript:;" v-show="!isAttention" class="btn-noattention flex-center" @click.stop="careAbout">关注</a>
			<a href="javascript:;" v-show="isAttention" class="btn-attention flex-center" @click.stop="careAbout">已关注</a>
		</div>
	</li>
</template>

<script>
	import '@/assets/js/layer041002.js';
	export default {
		name: '',
		props: {
			data: {
				type: [Object,Array],
				default: () => ({
					message: 'hello'
				})
			},
			dataindex: {
				type: [Number, String],
				default: 0
			},
			datahash: {
				type: [String],
				default: ''
			}
		},
		components: {
			
		},
		data() {
			let _this = this;
			let _realId = _this.data.member_id;
			let _type = 0;
			//console.log(_this.datahash);
			//关注类型 0关注人 1关注小区 2关注视频,点赞视频 3点赞别人评论
			//这里，因为是关注的人，所以固定为0
			if(~_this.datahash.indexOf('hisPersonFollow')){
				//如果是TA关注的人，则列表项id，传给app的id为followed_id
				//其他如我的粉丝，他的粉丝，关注小区的人，则列表项id，传给app的id为member_id
				_realId = _this.data.followed_id;
			};
			//console.log(_realId);
			return {
				'isAttention': _this.data.status===1?true:false,
				'realId': _realId,
				'realType': _type 
			}
		},
		created() {

		},
		methods: {
			careAbout() {
				let _this = this;
				if(_this.isAttention){
					_this.careAboutAjax();
					//取消询问的弹框
//					layer.open({
//					    content: '确定取消关注吗？',
//					    btn: ['确定', '取消'],
//					    yes: function(_index){
//					    	_this.careAboutAjax();
//					        layer.close(_index);
//					    }
//					});
				}else{
					_this.careAboutAjax();
				}
			},
			careAboutAjax() {
				let _this = this;
				//console.log(_this.realId);
				_this.axios({
					method: 'post',
					url: '/api/follow/addOrUpdateFollow',
					responseType: 'json',
					data: {
						'authToken': _this.$route.query.token,
						'type': _this.realType,
						'followed_id': _this.realId
					}
				})
				.then(function(response) {
					if(response.data.code == 200) {
						_this.isAttention = !_this.isAttention;
						_this.$emit('updateCare',{
							'attention': _this.isAttention,
							'id': _this.realId
						});
					} else {
						layer.tipsX(response.data.message);
					}
				})
				.catch(function(error) {
					layer.tipsX(error);
				});
			},
			goPage() {
				let _this = this;
				console.log(_this.realId);
				_this.common.Compatible({
					paraIos: {
						'route': 'onUserHomePage',
						'userId': Number(_this.realId)
					},
					fnAndroid() {
						window.android.onUserHomePage(String(_this.realId));
					},
					fn() {
						console.warn('error');
					}
				});
			}
		},
		computed: {
			typeClass() {
				switch (this.data.label_name){
					case '业主':
						return 'yezu';
					case '租房者':
						return '';
					case '买房者':
						return '';
					case '经纪人':
						return 'jingjiren';
					default:
						return '';
				}
			}
		}
	}
</script>

<style scoped>
	li{
		padding: 0 .28rem;
		height: 1.34rem;
		background-color: white;
	}
	li>div{
		height: 100%;
	}
	li>div:nth-of-type(1){
		overflow: hidden;
		align-items: center;
	}
	li>div:nth-of-type(1)>img{
		width: 1rem;
		height: 1rem;
		border-radius: 50%;
	}
	li>div:nth-of-type(2){
		width: 4.45rem;
		padding-left: .3rem;
		flex-direction: column;
	}
	li>div:nth-of-type(2)>div:nth-of-type(1){
		width: 4.45rem;
		padding-top: .24rem;
		overflow: hidden;
	}
	li>div:nth-of-type(2)>div:nth-of-type(1)>span{
		font-size: .32rem;
		/*font-weight: bold;*/
		font-weight: 600;
		color: #333;
	}
	li>div:nth-of-type(2)>div:nth-of-type(1)>mark{
		font-size: .18rem;
		height: .3rem;
		line-height: .3rem;
		padding: 0 .05rem;
		border-radius: .05rem;
		margin-left: .1rem;
	}
	li>div:nth-of-type(2)>div:nth-of-type(1)>mark.yezu{
		background-color: #FEF7E5;
		color: #FCB404;
	}
	li>div:nth-of-type(2)>div:nth-of-type(1)>mark.jingjiren{
		background-color: #EEF4FF;
		color: #659CFF;
	}
	li>div:nth-of-type(2)>div:nth-of-type(2){
		font-size: .28rem;
		color: #666;
		/*padding-top: .02rem;*/
		padding-top: .05rem;
	}
	li>div:nth-of-type(3){
		align-items: center;
	}
	li>div:nth-of-type(3)>a{
		width: 1.2rem;
	    height: .52rem;
	    border-radius: .26rem;
	    font-size: .28rem;
	    box-sizing: border-box;
	}
	.btn-attention{
		border: 1px solid #999;
		color: #999;
	}
	.btn-noattention{
		color: white;
		background-color: #659BFF;
	}
</style>


