<template>
	<div>
		<ul>
			<li v-for="(item, index) in mainDataList" :key="item.id" :data="item" is="common-questions-li" :dataindex="index"></li>
		</ul>
	</div>
</template>

<script>
	import '@/assets/js/layer041002.js';
	import commonQuestionsLi from '@/components/my/commonQuestionsLi';
	export default {
		name: '',
		components: {
			'common-questions-li': commonQuestionsLi
		},
		created() {
			let _this = this;
			_this.axios({
				method: 'get',
				url: '/api/question/getList',
				responseType: 'json'
			})
			.then(function(response) {
				if(response.data.code == 200) {
					_this.mainDataList = response.data.data;
				} else {
					layer.tipsX(response.data.msg);
				}
			})
			.catch(function(error) {
				layer.tipsX(error);
			});
		},
		data: () => ({
			'mainDataList': []
		}),
		methods: {

		},
		computed: {

		}
	}
</script>

<style>
	
</style>


