<template>
	<div>
		<ul>
			<li v-for="(item, index) in toReportList" :key="item.id" :data="item" is="to-report-li" :dataindex="index" @report="reportArr"></li>
		</ul>

		<div class="tijiao" @click="tijiao">
			提交
		</div>
	</div>
</template>

<script>
	import '@/assets/js/layer041002.js';

	import toReportLi from '@/components/toReport/toReportLi';

	export default {
		name: '',
		components: {
			'to-report-li': toReportLi,
		},
		data: () => ({
			toReportList: [],
			arr: [],
		}),
		created() {
			let _this = this;

			_this.axios({
					method: 'post',
					url: '/api/report/getList',
					responseType: 'json',
					data: {
						'authToken': _this.$route.query.token,
					}
				})
				.then(function(response) {
					if(response.data.code === 200) {
						_this.toReportList = response.data.data;

					} else {
						layer.tipsX(response.data.message);

					}
				})
				.catch(function(error) {
					layer.tipsX(error);

				});

		},
		methods: {
			tijiao() {
				let _this = this;
				_this.axios({
						method: 'post',
						url: '/api/report/addReport',
						responseType: 'json',
						data: {
							'content_id': _this.arr,
							'authToken': _this.$route.query.token,
							'video_id': _this.$route.query.video_id,
						}
					})
					.then(function(response) {
						if(response.data.code == 200) {
							layer.tipsX('提交成功');
							setTimeout(function() {
								_this.common.Compatible({
									paraIos: {
										'route': 'finishReport',
									},
									fnAndroid() {
										window.android.finishReport();
									},

									fn() {
										console.warn('error');
									}
								})
							}, 2001);
						} else {
							if(response.data.message == '传入参数不能为空') {
								layer.tipsX('提交内容为空');
							} else {
								layer.tipsX(response.data.message);
//								setTimeout(function() {
//									_this.common.Compatible({
//										paraIos: {
//											'route': 'finishReport',
//										},
//										fnAndroid() {
//											window.android.finishReport(true);
//										},
//
//										fn() {
//											console.warn('error');
//										}
//									})
//								}, 2001);

							}

						}
					})
					.catch(function(error) {
						layer.tipsX(error);
					});
			},
			reportArr(u) {
				let _this = this;

				if(u.isSelected) {
					_this.arr.push(u.id);

				} else {
					let index = _this.arr.indexOf(u.id);
					_this.arr.splice(index, 1);
				}
				console.log(_this.arr);
			}
		}

	}
</script>

<style scoped>
	.to-report {
		box-sizing: border-box;
		width: 100%;
		height: 1.2rem;
		line-height: 1.2rem;
		background: #fff;
		margin-top: .04rem;
		font-size: .28rem;
		color: #666;
	}
	
	.to-report-text {
		margin-left: .38rem;
	}
	
	.to-report-pic {
		float: right;
		width: .48rem;
		height: .48rem;
		margin: .4rem .4rem 0 0;
		background: url(images/icon_selected@2x.png) no-repeat center center/100% 100%;
	}
	
	.to-report-pictijiao {
		background: url(images/icon_unselected@2x.png) no-repeat center center/100% 100%;
	}
	
	.to-report-notijiao {
		background: url(images/icon_unselected@2x.png) no-repeat center center/100% 100%;
	}
	
	.tijiao {
		width: 100%;
		height: 1rem;
		background: #659CFF;
		font-size: .38rem;
		color: #FFF;
		text-align: center;
		line-height: 1rem;
		position: fixed;
		bottom: 0;
		left: 0;
	}
</style>


