//公用js文件
let ServerHost = 'https://yftest.fujuhaofang.com';
//let ServerHost = 'https://hifang.fujuhaofang.com';
if(~location.origin.indexOf('https://hifang.fujuhaofang.com') || ~location.origin.indexOf('https://hifang-pre.fujuhaofang.com')) {
	ServerHost = location.origin;
};
import axios from 'axios';
import '@/assets/js/layer041002.js';
export default {
	ServerHost,
	defaultSign: '看房小能手',
	isAndroid() {
		//判断是不是安卓设备
		let u = navigator.userAgent;
		return(~u.indexOf('Android') || ~u.indexOf('Linux')) ? true : false;
	},
	isIos() {
		//判断是不是ios设备
		return ~navigator.userAgent.indexOf('iPhone') ? true : false;
	},
	isWeixinBrowser() {
		//判断是不是微信浏览器
		return ~navigator.userAgent.indexOf('MicroMessenger') ? true : false;
	},
	isMobile(text) {
		//验证手机号码格式是否正确
		return /^1\d{10}$/.test(text) ? true : false;
	},
	backNowDate(s) {
		//返回当前日期，以2018-05-11的格式, a.toLocaleDateString();返回的是2018/5/11这样的格式
		let g = arguments.length === 0 ? '-' : s,
			a = new Date(),
			f = v => (v < 10 ? ('0' + v) : ('' + v));
		return a.getFullYear() + g + f(a.getMonth() + 1) + g + f(a.getDate());
	},
	timestampToTime(timestamp) {
		//把时间戳转为2018-09-10 17:09:06这样的时间格式
        let date = new Date(timestamp * 1000),//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        	f = v => (v < 10 ? ('0' + v) : ('' + v)),
        	Y = date.getFullYear() + '-',
	        M = f(date.getMonth()+1) + '-',
	        D = f(date.getDate()) + ' ',
	        h = f(date.getHours()) + ':',
	        m = f(date.getMinutes()) + ':',
	        s = f(date.getSeconds());
        return Y+M+D+h+m+s;
   },
	urlDeal(urlStr, httpStr) {
		//处理
		return urlStr.replace(/(http|https):\/\//g, (httpStr ? (httpStr + ':') : location.protocol) + '//');
	},
	hideTel(str) {
		//隐藏手机号码中间四位，展示为*号
		if(str) {
			let _arr = String(str).split('');
			_arr.splice(3, 4, '****');
			return _arr.join('');
		} else {
			return str;
		}
	},
	transCn(num) {
	    let e = String(num).split(''),
			a = [];
		e.forEach((v,i)=>{
			a[i] = ['零','一','二','三','四','五','六','七','八','九'][e[i]];
		});
	    return a.join('');
	},
	jineComma(s, n) {
		//金额以32, 348, 000的形式显示
		if(s) {
			if(n === 0) {
				let a = String(parseInt(s)),
					l = a.split('.')[0].split('').reverse(),
					t = '';
				for(let i = 0; i < l.length; i++) {
					t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? ',' : '');
				};
				return t.split('').reverse().join('').replace(/\,/g, ', ');
			} else {
				let n = n > 0 && n <= 20 ? n : 2,
					a = parseFloat((s + '').replace(/[^\d\.-]/g, '')).toFixed(n) + '',
					l = a.split('.')[0].split('').reverse(),
					r = a.split('.')[1],
					t = '';
				for(let i = 0; i < l.length; i++) {
					t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? ',' : '');
				};
				return(t.split('').reverse().join('') + '.' + r).replace(/\,/g, ', ');
			};
		} else {
			return s;
		}
	},
	dealJineNum(num) {
		//处理金额类的数字
		if(isFinite(num)) {
			if(parseInt(num) == num || num.toString().split('.')[1].length < 3) {
				return num;
			} else {
				return Number((num).toFixed(2));
			}
		} else {
			return 0;
		};
	},
	duringRequest(){
		//请求发送状态判断
		let arr = [...arguments];//arguments本身不是数组，是一个特殊的对象，后面的调用会使arguments发生变化，所以需要缓存起来
		axios.interceptors.request.use((config) => {
			arr.forEach((a, i) => {
				if(~config.url.indexOf(a.urlStr)){
					a.startAction();//执行请求开始的操作
				};
			});
			
			return config;
		}, (error) => {
			return Promise.reject(error);
		});
		
		//请求返回状态判断
		axios.interceptors.response.use((res) => {
			arr.forEach((a, i) => {
				if(~res.config.url.indexOf(a.urlStr)){
					a.endAction();//执行请求结束的操作
				};
			});
			return res;
		}, (error) => {
			return Promise.reject(error);
		});
	},
	h5Page(token, fn) {
		//token验证的接口封装
		let _this = this;
		axios({
			method: 'get',
			url: _this.ServerHost + '/api/user/userinfo',
			responseType: 'json',
			data: {
				'authToken': token
			}
		})
		.then(function(response) {
			if(response.data.code == 200) {
				//注意webview里的localStorage为null的情况,有些webview缺少对这个本地存储的设置
				localStorage.setItem('token', response.data.data.token); //存储登录的token值
				localStorage.setItem('userid', response.data.data.id); //存储用户id
				localStorage.setItem('userlevel', response.data.data.level); //存储用户id
				localStorage.setItem('userphone', response.data.data.phone); //存储用户名字
				localStorage.setItem('username', response.data.data.name); //存储用户电话号码
				localStorage.setItem('userinfo', encodeURIComponent(JSON.stringify(response.data.data))); //存储用户所有信息
				fn && fn();
			} else {
				layer.tipsX(response.data.msg);
				return false;
			}
		})
		.catch(function(error) {
			layer.tipsX(error);
		});
	},
	Compatible(obj) {
		//和app进行交互的方法封装
		if(this.isIos()) {
			try {
				if(obj['hrefIos']) {
					window.location.href = obj['hrefIos'];
				} else {
					window.webkit.messageHandlers.AppModel.postMessage(obj.paraIos); //webkit.messageHandlers.AppModel.postMessage可以直接调用，ios系统都支持
				}
			} catch(e) {
				obj.fn();
			};
		} else if(this.isAndroid()) {
			try {
				obj.fnAndroid(); //android.backPressed()是安卓提供的方法,定义在webview里
			} catch(e) {
				//alert(e.name + '\n' + e.message);
				obj.fn();
			}
		} else {
			console.log("other device");
			obj.fn();
		};
	},
	//记录用户行为的公用方法
	userBehavior(obj,fn) {
		axios({
			method: 'post',
			url: '/api/behavior/insertBehavior',
			responseType: 'json',
			data: obj
		})
		.then(function(response) {
			if(response.data.code === 200) {
				
			} else {
				layer.tipsX(response.data.message);
			};
			fn && fn();
		})
		.catch(function(error) {
			layer.tipsX(error);
		});
	}
};


