<template>
	<li>
		<section class="flex">
			<div class="flex" @click="goPage(data)">{{data.name}}</div>
			<div>
				<p>{{dealNum}}元/m²</p>
				<p>最新成交价</p>
			</div>
			<div>
				<p :class="{'fall-price':false}">0.20%</p>
				<p>较上月跌涨</p>
			</div>
		</section>
	</li>
</template>

<script>
	import '@/assets/js/layer041002.js';
	export default {
		name: '',
		props: {
			data: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			},
			dataindex: {
				type: [Number, String],
				default: 0
			}
		},
		components: {
			
		},
		data: () => ({
			
		}),
		created() {

		},
		methods: {
			goPage(_d) {
				let _this = this;
				console.log(_d);
				console.log(_d.followed_id);
				_this.common.Compatible({
					paraIos: {
						'route': 'onHouseDetails',
						'houseId': Number(_d.followed_id),
						'houseName': String(_d.name)
					},
					fnAndroid() {
						window.android.onHouseDetails(String(_d.followed_id), String(_d.name));
					},
					fn() {
						console.warn('error');
					}
				});
			}
		},
		computed: {
			dealNum() {
				return parseInt(this.data.average_price);
			}
		}
	}
</script>

<style scoped>
	li{
		padding: 0 .28rem;
		background-color: white;
	}
	li>section{
		height: 1.34rem;
		box-sizing: border-box;
		border-bottom: 1px solid #f0f0f0;
	}
	li:nth-last-of-type(1)>section{
		border-bottom: none;
	}
	li>section>div{
		height: 100%;
	}
	li>section>div:nth-of-type(1){
		overflow: hidden;
		flex: 2.6rem;
		align-items: center;
		color: rgb(51, 51, 51);
		font-size: .32rem;
		font-weight: bold;
	}
	li>section>div:nth-of-type(2){
		flex: 2.5rem;
	}
	li>section>div:nth-of-type(2)>p:nth-of-type(1){
		font-size: .36rem;
		color: rgb(74, 74, 74);
		padding-top: .23rem;
	}
	li>section>div:nth-of-type(2)>p:nth-of-type(2){
		font-size: .26rem;
		color: rgb(153, 153, 153);
		padding-top: .07rem;
	}
	li>section>div:nth-of-type(3){
		flex: auto;
	}
	li>section>div:nth-of-type(3)>p:nth-of-type(1){
		font-size: .46rem;
		color: rgb(233, 82, 92);
		padding-top: .17rem;
		position: relative;
	}
	li>section>div:nth-of-type(3)>p:nth-of-type(1)::after{
		content: '';
		position: absolute;
		width: .16rem;
		height: .46rem;
		right: 0;
		bottom: 0;
		background: url(images/icon_up@2x.png) no-repeat center center/.16rem .22rem;
	}
	li>section>div:nth-of-type(3)>p:nth-of-type(1).fall-price{
		color: #43B27C;
	}
	li>section>div:nth-of-type(3)>p:nth-of-type(1).fall-price::after{
		background: url(images/icon_down@2x.png) no-repeat center center/.16rem .22rem;
	}
	li>section>div:nth-of-type(3)>p:nth-of-type(2){
		font-size: .26rem;
		color: rgb(153, 153, 153);
		padding-top: .07rem;
		text-align: center;
	}
</style>


