<template>
	<div class="por">
		<div class="bg-image-area oh">
			<img :src="bgImage" class="fl" />
		</div>
		<div class="main-area poa bsb">
			<div class="input-area">
				<div class="flex input-box">
					<div>
						<input type="tel" v-model.trim="phone" placeholder="请输入手机号" />
					</div>
				</div>
				<div class="flex input-box">
					<div>
						<input type="text" v-model.trim="code" placeholder="请输入验证码" />
					</div>
					<div class="flex verification-code-area por">
						<div @click="getCode" v-show="!isGetCode">获取验证码</div>
						<div v-show="isGetCode">{{countSecond}}s</div>
					</div>
				</div>
				<div class="btn-area">
					<a class="flex-center" href="javascript:;" @click="submitOk" v-show="!isSaving">提交</a>
					<a class="flex-center" href="javascript:;" v-show="isSaving">提交中ing</a>
				</div>
			</div>
			<div class="qr-code-area">
				<div class="qr-code-img-area flex">
					<img :src="qrcodeImage" />
				</div>
				<div class="qr-code-text-area">
					<p>长按识别二维码下载嗨房</p>
					<p>观看更多真实租房买房视频</p>
				</div>
			</div>
		</div>
		<div class="redpack-mask-area" v-if="redpackMaskShow">
			<div>
				<div class="redpack-top-area">
					<div>{{repackMoney}}</div>
					<div class="flex-center">
						<a href="javascript:;" class="flex-center" @click="goPage">立即领取</a>
					</div>
				</div>
				<div class="redpack-close-area flex-center"><img :src="redpackCloseImage" @click="closeRedpackMask" /></div>
			</div>
		</div>
	</div>
</template>

<script>
	import '@/assets/js/layer041002.js';
	import bgImage from '@/components/share/images/invate_img_bg@2x.jpg';
	import redpackImage from '@/components/share/images/red_pack@2x.png';
	import redpackCloseImage from '@/components/share/images/icon_close copy@2x.png';
	import qrcodeImage from '@/assets/images/download_qrcode.png';
	export default {
		name: '',
		props: {
			data: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			},
			dataindex: {
				type: [Number, String],
				default: 0
			}
		},
		components: {
			
		},
		data: () => ({
			bgImage,
			qrcodeImage,
			redpackCloseImage,
			repackMoney: 0,
			redpackMaskShow: false,//红包区域是的mask是否显示出来
			isGetCode: false,
			countSecond: 60,
			phone: '',
			code: '',
			isSaving: false
		}),
		created() {
			let _this = this;
			//记录用户操作行为
			_this.common.userBehavior({
				'id': _this.$route.query.userid,
				'type': 18
			});
			
			_this.common.duringRequest({
				'urlStr': '/admin/member/bandPhone',
				startAction() {
					_this.isSaving = true;
				},
				endAction() {
					_this.isSaving = false;
				}
			});
			
		},
		mounted() {
			let _this = this;
			
		},
		methods: {
			goPage() {
				let _this = this;
				_this.$router.push({path: '/weixinDownload'});
			},
			closeRedpackMask() {
				let _this = this;
				_this.redpackMaskShow = false;
			},
			getCode() {
				let _this = this;
				_this.isGetCode = true;
				_this.axios({
					method: 'get',
					url: '/admin/Member/sendsms/'+_this.phone,
					responseType: 'json',
					data: {}
				})
				.then(function(response) {
					if(response.data.code === 200 && response.data.data) {
						layer.tipsX('验证码已发送，请注意查收');
					} else {
						layer.tipsX(response.data.message);
					}
				})
				.catch(function(error) {
					layer.tipsX(error);
				});
				
				//点击发送验证码的同时已经开始计时
				let _timer = setInterval(function(){
					if(_this.countSecond <= 1){
						clearInterval(_timer);
						_this.isGetCode = false;
						_this.countSecond = 60;
					}else{
						_this.countSecond -= 1;
					}
				},1000);
			},
			submitOk() {
				let _this = this;
				//userid为分享的app操作用户的id,
				//member_id为微信授权后，当前浏览用户的id
				//from为增加的内容
				if(_this.common.isMobile(_this.phone)){
					_this.axios({
						method: 'post',
						//url: '/admin/member/checkInvite',
						url: '/admin/member/bandPhone',
						responseType: 'json',
						data: {
							'member_id': _this.$route.query.member_id,
							'user_id': _this.$route.query.userid,
							'phone': _this.phone,
							'code': _this.code,
							'from': 'web'
						}
					})
					.then(function(response) {
						if(response.data.code === 200 && response.data.data) {
							layer.open({
							    content: '注册成功',
							    btn: ['确定'],
							    yes: function(_index){
							        layer.close(_index);
							        _this.repackMoney = response.data.data.icon;
							        _this.redpackMaskShow = true;
							    }
							});
							
						} else {
							layer.tipsX(response.data.message);
						}
					})
					.catch(function(error) {
						layer.tipsX(error);
					});
				}else{
					layer.tipsX('手机号码格式不正确');
				};
			}
		},
		computed: {

		}
	}
</script>

<style scoped>
	.bg-image-area>img{
		width: 100%;
		pointer-events: none;
		/*禁止图片点击事件，因为微信浏览器里的图片会点击放大的效果*/
	}
	.main-area{
		width: 100%;
		top: 7.6rem;
		left: 0;
		padding: 0 .6rem;
	}
	.input-area>div{
		height: .92rem;
	}
	.input-area>div.input-box{
		margin-bottom: .4rem;
		background-color: #FFF0FE;
	}
	.input-area input{
		display: block;
		/*height: 100%;*/
		height: .92rem;
		width: 4rem;
		color: #333;
		font-size: .36rem;
		text-indent: 1em;
		display: flex;
		align-items: center;
	}
	.input-area input::-webkit-input-placeholder{
		/*设置placeholder的独自样式*/
		font-size: .32rem;
		color: #999;
	}
	.verification-code-area{
		align-items: center;
		justify-content: center;
		flex: 2.3rem 0 0;
	}
	.verification-code-area>div{
		width: 100%;
		height: 100%;
		line-height: .92rem;
		text-align: center;
		color: #FF205D;
	}
	.verification-code-area::before{
		content: '';
		width: 1px;
		height: .4rem;
		background-color: #FF205D;
		position: absolute;
		left: 0;
		top: .26rem;
	}
	.btn-area>a{
		width: 100%;
		height: 100%;
		line-height: .92rem;
		border-radius: .46rem;
		background-color: yellow;
		color: #FF205D;
		font-size: .38rem;
	}
	
	.qr-code-area{
		padding-top: 1rem;
	}
	.qr-code-img-area{
		align-items: center;
		justify-content: center;
	}
	.qr-code-img-area>img{
		display: block;
		width: 2.4rem;
		height: 2.4rem;
		object-fit: cover;
	}
	.qr-code-text-area>p{
		text-align: center;
		padding-top: .1rem;
		color: white;
		font-size: .24rem;
	}
	
	/*红包区域补充*/
	.redpack-mask-area{
		position: fixed;
		top: 0;
		left: 0;
		height: calc(100vh - 3.56rem);
		width: 100vw;
		padding-top: 3.56rem;
		background-color: rgba(0, 0, 0, .7);
	}
	.redpack-top-area{
		height: 6.12rem;
		background: url(images/red_pack@2x.png) no-repeat center center/5.14rem 6.12rem;
	}
	.redpack-top-area>div:nth-of-type(1){
		padding-top: 2.2rem;
		padding-left: 3.5rem;
		font-size: 2rem;
		color: #FFF6F6;
	}
	.redpack-top-area>div:nth-of-type(2)>a{
		width: 3.94rem;
		height: .96rem;
		border-radius: .48rem;
		background-color: #F8E71C;
		font-size: .36rem;
		color: #FF515E;
	}
	.redpack-close-area{
		padding-top: .1rem;
	}
	.redpack-close-area>img{
		width: .96rem;
	}
</style>


