<template>
	<img :src="src" :onerror="onErrorImg" />
</template>

<script>
	import onErrorImgAvatar from '@/assets/images/ic_default_headpic.png';
	import onErrorImgHouse from '@/assets/images/house_zhanwei.jpg';
	import onErrorImgDiv from '@/assets/images/ic_default_div.png';
	export default {
		props: {
			datasrc: {
				type: String,
				default: () => 'hello'
			},
			imgtype: {
				type: String,
				default: () => 'avatar'
			}
		},
		data() {
			let _this = this;
			let _bitmap = onErrorImgAvatar;
			if(_this.imgtype === 'house'){
				_bitmap = onErrorImgHouse;
			}else if(_this.imgtype === 'div'){
				_bitmap = onErrorImgDiv;
			}else{
				//默认是头像的那张占位图
			};
			return {
				'bitmap': _bitmap,
				'src': _this.datasrc?_this.datasrc:_bitmap,
				'onErrorImg': 'this.src="' + _bitmap + '"'
			};
		},
		watch: {
			datasrc(newValue, oldVal) {
				this.src = newValue?newValue:this.bitmap;//当父组件传入的datasrc更新时，把src改为新值
			}
		}
	}
</script>

<style>
	.img-error-div{
		font-size: 0;
		background-color: #e0e0e0;
		width: 100%;
		height: 100%;
	}
</style>


