<template>
	<li class="pointer-click-item">
		<figure>
			<img-error :datasrc="data.video_cover" :imgtype="'house'"></img-error>
		</figure>
		<figcaption>{{numRoom(data.num_room)}}室{{numRoom(data.num_hall)}}厅/{{data.area}}m² /{{data.price}}{{data.video_type=='1'?'万':'元/月'}}</figcaption>
		<div class="house-title-div">{{data.name}}</div>
	</li>
</template>

<script>
	import '@/assets/js/layer041002.js';
	export default {
		name: '',
		props: {
			data: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			},
			dataindex: {
				type: [Number, String],
				default: 0
			},
			behaviorid: {
				type: [Number, String],
				default: 0
			}
		},
		components: {

		},
		data: () => ({

		}),
		created() {
			let _this = this;

		},
		mounted() {
			let _this = this;

		},
		methods: {
			numRoom(m) {
				return this.common.transCn(m);
			}
		},
		computed: {

		}
	}
</script>

<style scoped>
	li{
		flex: 3.38rem 0 0;
		padding: .16rem .08rem;
	}
	figure{
		width: 100%;
		height: 3.86rem;
	}
	figure>img{
		width: 100%;
		height: 100%;
		object-fit: cover;
		border-radius: .08rem;
	}
	figcaption{
		font-size: .24rem;
		line-height: .36rem;
		color: #4a4a4a;
		padding: .16rem 1em 0;
	}
	.house-title-div{
		font-size: .26rem;
		line-height: .4rem;
		color: #4a4a4a;
		padding: 0 1em;
		max-width: 3.38rem;
		word-break: break-all;
	}
</style>


