// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
//共用样式文件需要在App引入之前引入
import '@/assets/css/reset_xsf0409.css';//reset css文件
import '@/assets/css/public.css';//共用css文件
import '@/assets/css/layer.css';//layer弹出层的样式文件
//import 'swiper/dist/css/swiper.css';//轮播图插件的css文件
import App from './App'
import router from './router'
import axios from 'axios'
import qs from 'qs'
import VueAxios from 'vue-axios'
//import VueAwesomeSwiper from 'vue-awesome-swiper';
//Vue.use(VueAwesomeSwiper);
import common from '@/assets/js/public.js';
Vue.prototype.common = common;

axios.defaults.timeout = 20000;//设置请求最大时长，超时将取消
axios.defaults.baseURL = common.ServerHost;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
//axios.defaults.headers.post['X-Requested-With'] = 'XMLHttpRequest';

axios.interceptors.request.use((config) => {
	if(config.method === 'post') {
		config.data = qs.stringify(config.data);
	}else if(config.method === 'get'){
		config.params = config.data;
		delete config.data;//删除原data属性
	}else{};
	return config;
}, (error) => {
	return Promise.reject(error);
});

//返回状态判断
axios.interceptors.response.use((res) => {
//福居里的代码太乱，接口返回数据格式没有统一，不能这样处理
//	if(res.data.status !== 0) {
//		return Promise.reject(res);//错误返回catch
//	};
	return res;//正确返回then
}, (error) => {
	//404等问题可以在这里处理
	return Promise.reject(error);
});

//vue-axios是为了注册axios更加规范而引入的
//下面的use顺序不能写反
Vue.use(VueAxios, axios);
//把common注册到Vue全局
Vue.config.productionTip = true;

import headerPulic from '@/components/publicEg/headPublic';
import errorImg from '@/components/publicEg/errorImg';
import loadingGif from '@/components/publicEg/loadingGif'
Vue.component('header-pulic', headerPulic);//注册全局组件
Vue.component('img-error', errorImg);//注册全局组件
Vue.component('loading-gif', loadingGif);//注册全局组件

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



