<template>
	<div class="float-window-top flex">
		<div>
			<img-error :datasrc="data.headimgurl"></img-error>
		</div>
		<div>
			<p>{{data.nickname}}</p>
			<p>我在用嗨房，邀你一起选房子</p>
		</div>
		<div>
			<a class="flex-center" href="javascript:;" @click="goPage">打开</a>
		</div>
	</div>
</template>

<script>
//	import srcImgX from '@/components/headPublicEg/img/pbl_cha.png';
	export default {
		name: 'download-float',
		props: {
			data: {
				type: Object,
				default: () => ({
					'message': 'hello'
				})
			}
		},
		data:() => ({
			
		}),
		created() {
			
		},
		methods: {
			goPage() {
				let _this = this;
				_this.$router.push({path: '/weixinDownload'});
			}
		},
	}
</script>

<style>
	.float-window-top {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 999999;
		background: white;
		height: 1.3rem;
		width: 7.5rem;
	}
	
	.float-window-top>div:nth-of-type(1){
		display: flex;
		align-items: center;
		justify-content: center;
		width: 1.5rem;
	}
	.float-window-top>div:nth-of-type(1)>img{
		width: .88rem;
		height: .88rem;
		border-radius: 50%;
	}
	.float-window-top>div:nth-of-type(2){
		width: 4.5rem;
	}
	.float-window-top>div:nth-of-type(2)>p:nth-of-type(1){
		color: #333;
		font-size: .32rem;
		padding: .24rem 0 0;
	}
	.float-window-top>div:nth-of-type(2)>p:nth-of-type(2){
		color: #666;
		font-size: .28rem;
	}
	.float-window-top>div:nth-of-type(3){
		width: 1.7rem;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.float-window-top>div:nth-of-type(3)>a{
		/*display: flex;*/
		width: 1.2rem;
		height: .52rem;
		border-radius: .26rem;
		background-color: #659BFF;
		/*align-items: center;
		justify-content: center;*/
		/*text-align: center;*/
		color: white;
		/*line-height: .52rem;*/
	}
	
</style>


